export const shoplist = (state = InitialState, action) => {
  switch(action.type){
    case 'SHOW_SHOPDETAIL':
      return state.map(item =>{
        if(item.id === action.id){
          item.isDetail = true;
          return item;
        } else {
          item.isDetail = false;
          return item;
        }
        }
      ) 
    case 'NEW_SHOPDETAIL':
      return action.shoplists;
    case 'SHOW_ALLITEM':
      console.log('action is =>', action);
      return InitialState;
    default:
      return InitialState;
  }
}